___CSS_LOADER_EXPORT___.push([module.id, ".starter{margin:0;padding:0}", "",{"version":3,"sources":["webpack://./resources/js/app/components/InsertCategoryAttribute.vue"],"names":[],"mappings":"AAgLA,SACI,QAAS,CACT,SACJ","sourcesContent":["<template>\n    <div class=\"card card-primary card-outline card-tabs\">\n        <div class=\"card-body\">\n            <div class=\"row invoice-info\">\n                <div class=\"col-sm-4 invoice-col\">\n                    <address>\n                        <b>კატეგორიის სახელი :</b>\n                        {{ additional.category.name }} <br />\n                        <b>ერთეული :</b> {{ additional.category.type }} <br />\n                    </address>\n                </div>\n            </div>\n            <hr />\n\n            <div class=\"row\">\n                <div class=\"col\">\n                    <ul class=\"starter\">\n                        <div class=\"mb-3\">\n                            <button\n                                @click=\"add\"\n                                type=\"button\"\n                                class=\"btn btn-success\"\n                            >\n                                <i class=\"fas fa-plus\"> დამატება</i>\n                            </button>\n                        </div>\n                        <hr />\n\n                        <template\n                            v-for=\"(item, index) in treeData\"\n                            :key=\"index\"\n                        >\n                            <TreeItem\n                                :setting=\"setting\"\n                                class=\"item\"\n                                :model=\"item\"\n                                :tree=\"\n                                    item.nested.length ? item.nested : treeData\n                                \"\n                                :id=\"index\"\n                            >\n                            </TreeItem>\n                        </template>\n                    </ul>\n\n                    <hr />\n\n                    <button\n                        @click=\"exit\"\n                        type=\"button\"\n                        class=\"btn btn-danger\"\n                        style=\"margin-right: 5px\"\n                    >\n                        <i class=\"far fa-window-close\"></i> გასვლა\n                    </button>\n\n                    <button\n                        :disabled=\"v$.$invalid\"\n                        @click=\"send\"\n                        type=\"\"\n                        class=\"btn btn-success\"\n                    >\n                        <i class=\"far fa-paper-plane\"></i> გაგზავნა\n                    </button>\n                </div>\n            </div>\n        </div>\n    </div>\n</template>\n\n<script>\nimport Util from \"Util\";\nimport TreeItem from \"./TreeItem.vue\";\nimport useVuelidate from \"@vuelidate/core\";\n\nexport default {\n    props: [\"user\", \"url\", \"model\", \"additional\", \"setting\"],\n    components: {\n        TreeItem,\n    },\n    data() {\n        return {\n            treeData: [],\n        };\n    },\n    setup(props) {\n        console.log(\"props\", props);\n        return { v$: useVuelidate() };\n    },\n    validations() {\n        return {\n            treeData: {},\n        };\n    },\n    computed: {\n        category() {\n            return this.setting.url.request.exit.replace(\n                \"new\",\n                this.additional.category.id\n            );\n        },\n    },\n    created() {\n        if (!this.model.length) {\n            this.model.push(Util.instance(this.model, true, this.additional));\n        }\n        this.treeData = this.model;\n    },\n    mounted() {\n        console.log(\"model\", this.model);\n    },\n    methods: {\n        exit() {\n            return (window.location.href = this.category);\n        },\n\n        add() {\n            // let lastIndex = this.treeData.slice(-1)[0].id + 1;\n            this.treeData.push(\n                Util.instance(this.model, true, this.additional)\n            );\n        },\n\n        send(e) {\n            e.preventDefault();\n\n            // console.log('Util.exit(this.model)', Util.exit(this.model))\n\n            let action = document\n                .querySelector(\"form#render\")\n                .getAttribute(\"action\");\n            let token = document\n                .querySelector('meta[name=\"csrf-token\"')\n                .getAttribute(\"content\");\n\n            this.v$.treeData.$touch();\n\n            this.$http\n                .post(action, Util.exit(this.model), {\n                    \"Content-Type\": \"application/json\",\n                    \"X-CSRF-TOKEN\": token,\n                })\n                .then(async (response) => {\n                    Util.useSwall(response).then((result) => {\n                        if (!response.data.success) {\n                            if (response.data.errs)\n                                response.data.errs.map((item) =>\n                                    this.$toast.error(item, {\n                                        position: \"top-right\",\n                                        duration: 7000,\n                                    })\n                                );\n                        }\n\n                        if (result.value) {\n                            window.location.replace(\n                                this.setting.url.request.index.replace(\n                                    \"api/\",\n                                    \"\"\n                                )\n                            );\n                        }\n                    });\n                })\n                .catch((e) => {\n                    this.$toast.error(e.response.statusText, {\n                        position: \"top-right\",\n                        duration: 7000,\n                    });\n                });\n        },\n    },\n};\n</script>\n\n<style>\n.starter {\n    margin: 0;\n    padding: 0;\n}\n</style>\n"],"sourceRoot":""}]);
